*, *::after, *::before {
	border: none;
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}

address {
	font-style: initial;
}

h1 {
	font-weight: normal;
}

h2 {
	font-weight: normal;
}

h3 {
	font-weight: normal;
}

h4 {
	font-weight: normal;
}

h5 {
	font-weight: normal;
}

h6 {
	font-weight: normal;
}

html {
	box-sizing: border-box;
	line-height: 1.5;
}

ol, ul {
	list-style-position: inside;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

:root {
	--animation-duration-default: 2s;
	--animation-iteration-count-default: infinite;
	--animation-timing-function-default: ease-in-out;
	--color-fax-paper: rgba(255,255,250,1);
	--color-grass: rgba(35,140,35,1);
	--color-greyn: rgba(170,180,170,1);
	--color-lava: rgba(255,55,25,1);
	--color-mangold: rgba(255,245,45,1);
	--color-papyrus: rgba(240,245,220,1);
	--color-pitch: rgba(20,25,25,1);
	--color-raspberred: rgba(235,95,110,1);
	--color-shadow: rgba(0,0,0,0.2);
	--font-size-huge: 20px;
	--font-size-large: 18px;
	--font-size-medium: 16px;
	--font-size-small: 14px;
	--font-size-tiny: 12px;
	--font-weight-bold: 700;
	--font-weight-bolder: 900;
	--letter-spacing-punch-card: 1rem;
	--padding-x-punch-card: 1.5em;
	--padding-y-punch-card: 1em;
	--transition-duration-default: 1s;
	--transition-property-default: all;
	--transition-timing-function-default: ease-in-out;
	--max-height-mobile-tiny: 568px;
	--max-width-mobile-tiny: 320px;
}

.site-navigation .cursor {
	animation-duration: var(--animation-duration-default, 2s);
	animation-iteration-count: var(--animation-iteration-count-default, infinite);
	animation-timing-function: var(--animation-timing-function-default, ease-in-out);
}

.site-footer, .site-header {
	font-family: PT-Mono, Consolas;
	user-select: none;
}

.site-footer, .site-navigation .breadcrumbs {
	overflow-x: auto;
	white-space: nowrap;
}

section[class], section.index .pages, section.index .pages .page, .gallery {
	align-items: center;
	display: flex;
	justify-content: center;
}

section.biography .fax-paper-content > .subsection > .category > .heading, section.biography .fax-paper-content > .subsection > .heading {
	grid-column: 1 / -1;
	grid-row: 1 / 2;
}

.site-footer, .site-header {
	padding: 0.25em 0.5em;
	position: fixed;
	width: 100%;
	z-index: 5;
}

.button-keyboard::after, .page-content::after, .page-content::before, .punch-card-row::after {
	content: "";
	display: block;
	position: absolute;
}

table tbody td:not(:last-child), table thead th:not(:last-child) {
	border-style: dashed;
	border-width: 0 0.0625em 0 0;
	padding: 0.125em 0.5em;
}

.punch-card a:focus .punch-hole, .punch-card a:hover .punch-hole, .punch-card-row:hover a .punch-hole {
	color: inherit;
	font-weight: inherit;
	letter-spacing: inherit;
}

.punch-card a:focus, .punch-card a:hover, .punch-card-row:hover a {
	color: initial;
	font-weight: initial;
	letter-spacing: initial;
}

.gallery .image-frame, .punch-card .punch-hole, .punch-card-row::after, .punch-card-row > .punch-card-row-filler, .punch-card a {
	transition-duration: var(--transition-duration-default, 1s);
	transition-property: var(--transition-property-default, all);
	transition-timing-function: var(--transition-timing-function-default, ease-in-out);
}

@keyframes blink {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes toss {
	0% {
		transform: rotate(15deg) translate(100%, 25%);
	}
	100% {
		transform: rotate(-1deg);
	}
}

.punch-card a:focus::before, .punch-card a:hover::before, .punch-card-row:hover a::before {
	margin-right: calc(var(--letter-spacing-punch-card, 1rem) / 2);
}

.punch-card a:focus .punch-hole, .punch-card a:hover .punch-hole, .punch-card-row:hover a .punch-hole {
	background-color: inherit;
	box-shadow: none;
	margin-right: 0;
}

a[href*="github"]::before {
	content: "\f09b";
}

a[href^="mailto:"]::before {
	content: "\f0e0";
}

a[href^="tel:"]::before {
	content: "\f3cd";
}

abbr {
	padding-right: .25em;
	position: relative;
}

abbr::after {
	content: "?";
	position: absolute;
	right: -0.1875em;
	top: -0.125em;
}

body {
	background-color: rgba(245, 250, 255, 0.5);
	padding: 2em 0;
}

details {
	margin-bottom: 0.5em;
	padding-left: 0.5em;
}

ol, ul {
	padding-left: 0.5em;
}

ol li:not(:last-child), ul li:not(:last-child) {
	margin-bottom: 0.5em;
}

ol li[data-order], ul li[data-order] {
	order: attr(data-order integer);
}

section[class] {
	flex-wrap: wrap;
}

section.biography {
	align-items: stretch;
	animation-duration: 1.5s;
	animation-iteration-count: 1;
	animation-name: toss;
	animation-timing-function: cubic-bezier(0.2, 1, 0.375, 1);
	display: grid;
	filter: drop-shadow(-0.25em 0.25em 0.5em var(--color-shadow, rgba(0, 0, 0, 0.2)));
	grid-template-areas: "fax-paper-strip-left fax-paper-content fax-paper-strip-right";
	grid-template-columns: 2em calc(100% - 4em) 2em;
	margin: auto;
	overflow: hidden;
}

section.biography ol, section.biography ul {
	list-style-type: none;
}

section.biography ol {
	justify-content: space-between;
}

section.biography ol li {
	align-items: center;
	display: grid;
	grid-column-gap: 0.5em;
	grid-row: 1 / -1;
	grid-template-columns: 12em 1em 1fr;
	grid-template-rows: minmax(min-content, 1.5em) minmax(min-content, 0.75em);
}

section.biography ol li::after {
	align-self: center;
	content: ">";
	display: block;
	grid-column: 2;
	grid-row: 1 / -1;
	margin-left: 0.25em;
}

section.biography ol li span {
	align-self: center;
	grid-column: 3;
	grid-row: span 2;
}

section.biography ol li sub {
	align-self: first baseline;
	grid-row: 2 / 3;
}

section.biography ol li sub, section.biography ol li time {
	grid-column: 1;
	justify-self: end;
}

section.biography ol li time {
	align-self: last baseline;
	grid-row: 1 / 2;
}

section.biography table tbody tr:hover {
	background-color: rgba(170, 180, 170, 0.15);
}

section.biography ul li {
	border-color: var(--color-greyn, darkgray);
	border-style: dotted;
	border-width: 0 0 0 2px;
	padding-left: 0.5em;
	position: relative;
}

section.biography ul li::before {
	bottom: -0.25em;
	content: "\1CDF";
	margin-left: -0.25em;
	position: absolute;
}

section.biography .fax-paper-content {
	background-color: var(--color-fax-paper, ivory);
	border-color: var(--color-greyn, darkgray);
	border-style: dashed;
	border-width: 0 0.125em;
	display: grid;
	grid-area: fax-paper-content;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	padding: 1em 0.25em 2em 0.125em;
}

section.biography .fax-paper-content > .subsection {
	display: grid;
	grid-column: 1 / -1;
	grid-row: span 1;
	grid-template-columns: auto;
	grid-template-rows: minmax(1.5em, 3em) 1fr;
}

section.biography .fax-paper-content > .subsection > .category {
	display: grid;
	grid-template-columns: auto;
	grid-template-rows: auto;
}

section.biography .fax-paper-content > .subsection > .heading span {
	padding-bottom: 0.25em;
	position: relative;
}

section.biography .fax-paper-content > .subsection > .heading span::after {
	border-width: 1px 0 0 0;
	bottom: 0;
	content: "\0307";
	top: 100%;
}

section.biography .fax-paper-content > .subsection > .heading span::after, section.biography .fax-paper-content > .subsection > .heading span::before {
	border-color: var(--color-greyn, darkgray);
	border-style: solid;
	display: block;
	left: 0;
	position: absolute;
	right: 0;
	top: 100%;
}

section.biography .fax-paper-content > .subsection > .heading span::before {
	border-width: 0 0 2px 0;
	bottom: 10%;
	content: "\0740";
	top: 90%;
}

section.biography .fax-paper-strip {
	background-color: var(--color-fax-paper, ivory);
	display: grid;
	grid-row-gap: 2em;
	grid-template-columns: 1fr;
	width: 2em;
	word-break: break-all;
	z-index: 0;
}

section.biography .fax-paper-strip:first-of-type {
	grid-area: fax-paper-strip-left;
	margin-top: -0.6875em;
}

section.biography .fax-paper-strip:last-of-type {
	grid-area: fax-paper-strip-right;
	margin-top: -0.5em;
}

section.biography .fax-paper-strip-hole {
	height: 1em;
}

section.biography .heading {
	background-color: rgba(190, 230, 65, 0.15);
}

section.contacts {
	height: calc(100vh - 4em);
}

section.index {
	display: block;
	margin: auto;
	max-width: 800px;
}

section.index .greeting {
	padding: 2em 0;
}

section.index .pages {
	flex-wrap: wrap;
	justify-content: space-around;
}

section.index .pages .page {
	flex-direction: column;
	margin-bottom: 3em;
	margin-left: auto;
	margin-right: auto;
	max-width: 256px;
}

section.index .pages .page-link {
	align-self: flex-end;
	max-width: 256px;
}

section.index .pages .page-tile {
	height: 256px;
	filter: drop-shadow(-0.25em 0.25em 0.5em var(--color-shadow, rgba(0, 0, 0, 0.2)));
	width: 256px;
}

section .subsection:not(:last-child) {
	margin-bottom: 2em;
}

table * {
	border-color: var(--color-greyn, darkgray);
}

table tbody td {
	padding: 0.125em 0.5em;
}

table thead {
	border-style: double;
	border-width: 0 0 0.25em 0;
}

table thead th {
	padding: 0 0.25em;
}

table thead th.heading {
	background: none;
}

table thead tr:not(:last-child) {
	border-style: solid;
	border-width: 0 0 0.125em 0;
}

.button-keyboard {
	background-color: #fff2d8;
	border-color: #ffdc96;
	border-style: solid;
	border-width: 1px;
	border-radius: 0.375em;
	box-shadow: -2px 4px 0 5px rgba(246, 237, 219, 0.75), -4px 6px 2px 6px rgba(226, 218, 201, 0.75), -6px 8px 4px 7px rgba(113, 113, 113, 0.85);
	margin: 1em;
	padding: 0.5em 1.25em 1em 3.25em;
	position: relative;
}

.button-keyboard::after {
	content: "\2B11";
	left: 15%;
	top: 35%;
	transform: scale(2, 2) translateY(-15%);
}

.button-keyboard:hover {
	box-shadow: -1px 2px 0 5px rgba(246, 237, 219, 0.75), -3px 4px 2px 6px rgba(226, 218, 201, 0.75), -5px 6px 4px 5px rgba(135, 135, 135, 0.85);
	transform: translate(-2px, 2px);
}

.category {
	margin-bottom: 1em;
}

.error {
	padding: 1em 2em;
}

.folder {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}

.gallery {
	flex-wrap: wrap;
}

.gallery .image-frame {
	border-color: rgba(20, 25, 25, 0.6);
	border-style: ridge;
	border-width: 1em;
	margin: 1em;
	position: relative;
	transition-duration: 0.4s;
	transition-property: scale();
	width: var(--max-width-mobile-tiny, 320px);
}

.gallery .image-frame:hover {
	transform: scale(1.1);
}

.gallery .image-frame img {
	display: block;
	height: auto;
	max-height: var(--max-height-mobile-tiny, 568px);
	max-width: var(--max-width-mobile-tiny, 320px);
	width: 100%;
}

.heading {
	padding-left: 2em;
}

.icon {
	margin-right: 1em;
	max-height: 1em;
}

.icon-error::before {
	content: "\f071";
}

.page-content {
	position: relative;
}

.page-content::after {
	background-color: rgba(245, 250, 255, 0.75);
	z-index: -1;
}

.page-content::after, .page-content::before {
	height: 100%;
	left: 0;
	top: 0;
	width: 100%;
}

.page-content::before {
	background-image: url(./../images/code.png);
	background-position: left top;
	background-repeat: repeat-y;
	filter: blur(2px);
	z-index: -2;
}

.punch-card {
	background-color: var(--color-papyrus, lightgoldenrodyellow);
	border-radius: 0.75em;
	clip-path: polygon(5% 0, 100% 0, 100% 100%, 0 100%, 0 10%);
	display: flex;
	flex-direction: column;
	height: calc(var(--max-width-mobile-tiny, 320px) - 4em);
	max-width: var(--max-height-mobile-tiny, 568px);
	overflow: hidden;
	padding-bottom: var(--padding-y-punch-card, 1em);
	padding-left: var(--padding-x-punch-card, 1.5em);
	padding-right: var(--padding-x-punch-card, 1.5em);
	padding-top: calc(var(--padding-y-punch-card, 1em) * 4);
	position: relative;
	width: 100%;
}

.punch-card::after {
	display: block;
	content: "";
	position: absolute;
	z-index: -1;
	border-color: var(--color-greyn, darkgray);
	border-style: solid;
	border-width: 0 1px;
	bottom: var(--padding-y-punch-card, 1em);
	left: var(--padding-x-punch-card, 1.5em);
	right: var(--padding-x-punch-card, 1.5em);
	top: var(--padding-y-punch-card, 1em);
}

.punch-card::before {
	display: block;
	content: "";
	position: absolute;
	z-index: -2;
	background-color: inherit;
	content: "3-1A-1S9.265ES :=: 1.A14 | z857";
	filter: brightness(90%) hue-rotate(120deg);
	height: 1.75em;
	margin-left: -1.3em;
	margin-top: -3.8em;
	padding-right: 2em;
	width: 100%;
}

.punch-card a::before {
	max-width: 1.5em;
}

.punch-card a, .punch-card address {
	align-items: center;
	box-shadow: -0.25em 0 var(--color-papyrus, lightgoldenrodyellow);
	display: inline-flex;
}

.punch-card .punch-hole {
	background-color: rgba(245, 250, 255, 0.5);
	box-shadow: inset -1px 1px var(--color-greyn, darkgray);
	margin-right: var(--letter-spacing-punch-card, 1rem);
	transition-property: background, margin;
}

.punch-card-row {
	align-items: center;
	display: flex;
	flex-basis: 100%;
	overflow: hidden;
	padding-left: 1em;
	position: relative;
}

.punch-card-row::after {
	background-color: var(--color-papyrus, lightgoldenrodyellow);
	bottom: 0;
	right: 1px;
	top: 0;
	transition-property: width;
	width: 0.5em;
}

.punch-card-row:hover address {
	margin: 0 auto;
}

.punch-card-row:hover > .punch-card-row-filler {
	display: none;
	opacity: 0;
}

.punch-card-row:nth-child(2), .punch-card-row:nth-child(6) {
	border-top: 1px dashed var(--color-greyn, darkgray);
}

.punch-card-row:nth-last-child(1) {
	border-top: 3px double var(--color-greyn, darkgray);
}

.punch-card-row > address:last-child > a > .punch-hole:last-child {
	margin-right: 0;
}

.punch-card-row > .punch-card-row-filler {
	opacity: 1;
	overflow: hidden;
	transition-property: opacity;
}

.punch-card-row > .punch-card-row-filler + address {
	padding-right: 1.625em;
}

.site-footer {
	align-items: flex-end;
	bottom: 0;
	display: flex;
}

.site-footer::after {
	content: ">";
}

.site-footer .text-light {
	margin: 0 0.125em;
}

.site-footer,
.site-header {
	background-color: rgba(245, 250, 255, 0.85);
}

.site-header {
	top: 0;
}

.site-navigation {
	display: flex;
}

.site-navigation ul {
	align-items: center;
	display: inline-flex;
	flex-direction: row;
	justify-content: flex-start;
	list-style-type: none;
}

.site-navigation .breadcrumb:not(:last-child)::after {
	content: "/";
	margin: 0 0.125em;
}

.site-navigation .breadcrumbs {
	align-items: baseline;
}

.site-navigation .breadcrumbs::after {
	content: ">";
}

.site-navigation .breadcrumbs::before {
	content: "S:/";
}

.site-navigation .cursor {
	animation-name: blink;
	margin: 0 0.125em;
}

